@import url('https://fonts.googleapis.com/css?family=Nunito');

body {
  margin: 0;
  padding: 0;
  font-family: Nunito, sans-serif;
}

.flex-container-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.flex-container-col {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.flex {
  -ms-flex: 1 1;
      flex: 1 1;
}

.grow { -webkit-transition: all .2s ease-in-out; -o-transition: all .2s ease-in-out; transition: all .2s ease-in-out; }
.grow:hover { -webkit-transform: scale(1.1); -ms-transform: scale(1.1); transform: scale(1.1); }

.pull-left {
  float: left;
}

@media (max-width : 600px) {
  .section-content {
    padding: 1em 1em 1em .5em;
    -ms-flex-pack: center;
        justify-content: center;
  }

  .section {
    padding-top: 1.5em;
  }
}

@media (min-width : 601px) and (max-width : 900px) {
  .section-content {
    -ms-flex-pack: center;
        justify-content: center;
  }

  .section {
    padding-top: 3em;
  }
}

@media (min-width : 901px) {
  .section-content {
    padding: 1em 3em 3em 3em;
    -ms-flex-pack: center;
        justify-content: center;
  }

  .section {
    margin: 0em .25em 0em .25em;
    padding-top: 3em;
  }
}


